.bg {
  position: absolute;
  top: 7.5rem;
  right: 3.7rem;
  z-index: 1;
  object-fit: cover;
  height: 60vh;
  border-radius: 50%;
  border: 1px solid lightblue;
  cursor: pointer;
  transition: transform 0.3s ease-in-out; /* Smooth transition */
  transform: scale(1); /* Initial scale */
}
.bg:hover {
  transform: scale(1.2); /* Scale up on hover */
}
.bg:active {
  transform: scale(0.9); /* Optional: Scale down when clicked */
}

.introContent {
  height: 100vh;
  width: 100vw;
  padding: 2rem;
  font-size: 3rem;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hello {
  font-size: 1.75rem;
  font-weight: 100;
}

.introName {
  color: yellow;
}

.introPara {
  font-size: medium;
  font-weight: 300;
  letter-spacing: 1px;
}

.btn {
  background: white;
  margin: 1rem 0;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 2rem;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.btn:hover {
  transform: scale(1.2);
}

.btnImg {
  object-fit: cover;
  margin: 0 0.25rem;
  height: 1rem;
}

@media screen and (max-width: 840px) {
  .bg {
    right: -10vw;
    z-index: -1;
  }

  .introContent {
    font-size: 10vw;
  }

  .hello {
    font-size: 4.5vw;
  }
}

@media screen and (max-width: 840px) {
  .bg {
    right: -32vw;
    z-index: -1;
  }
}
